import Link from 'next/link';
import { fetchPosts } from '@/lib/api';

interface Post {
  id: number;
  Title: string;
  Content: Array<{
    type: string;
    children: Array<{
      type: string;
      text: string;
    }>;
  }>;
  Slug: string;
  Thumbnail?: {
    id: number;
    url: string;
  };
}

function getPlainText(content: Post['Content']): string {
  return content
    .map(block => 
      block.children
        .map(child => child.text)
        .join('')
    )
    .join('\n');
}

export default async function HomePage() {
  const posts = await fetchPosts();

  if (!posts || posts.length === 0) {
    return (
      <main className="p-8">
        <h1 className="text-3xl font-bold mb-6">My Blog</h1>
        <p>No posts found. Please add some posts in the Strapi admin panel.</p>
      </main>
    );
  }

  return (
    <main className="p-8">
      <h1 className="text-3xl font-bold mb-6">My Blog</h1>
      <div className="grid gap-6">
        {posts.map((post: Post) => (
          <article key={post.id} className="p-4 border rounded-lg hover:shadow-md transition-shadow">
            <Link href={`/blog/${post.Slug}`}>
              <h2 className="text-xl font-semibold text-blue-600 hover:underline mb-2">
                {post.Title}
              </h2>
            </Link>
            <p className="text-gray-600">
              {getPlainText(post.Content).slice(0, 150)}...
            </p>
          </article>
        ))}
      </div>
    </main>
  );
}
