interface StrapiPost {
  id: number;
  documentId: string;
  Title: string;
  Content: Array<{
    type: string;
    children: Array<{
      type: string;
      text: string;
    }>;
  }>;
  Slug: string;
  createdAt: string;
  updatedAt: string;
  publishedAt: string;
  Thumbnail?: {
    id: number;
    url: string;
  };
}

export async function fetchPosts() {
  try {
    const apiUrl = `${process.env.NEXT_PUBLIC_API_URL}/api/posts?populate=*`;
    console.log('Fetching from URL:', apiUrl);
    
    const res = await fetch(apiUrl);
    if (!res.ok) {
      throw new Error(`HTTP error! status: ${res.status}`);
    }
    const data = await res.json();
    console.log('Raw Strapi response:', JSON.stringify(data, null, 2));

    // Check if data has the expected structure
    if (!data.data || !Array.isArray(data.data)) {
      console.error('Unexpected data structure:', data);
      return [];
    }

    // Log each post's structure
    data.data.forEach((post: StrapiPost, index: number) => {
      console.log(`Post ${index + 1} structure:`, {
        id: post.id,
        Title: post.Title,
        Content: post.Content,
        Slug: post.Slug
      });
    });

    // Validate each post has the required fields
    const validPosts = data.data.filter((post: StrapiPost) => {
      const isValid = post?.Title && 
                     Array.isArray(post?.Content) && 
                     post?.Content.length > 0 && 
                     post?.Slug;
      if (!isValid) {
        console.warn('Invalid post structure:', post);
      }
      return isValid;
    });

    console.log('Number of valid posts:', validPosts.length);
    return validPosts;
  } catch (error) {
    console.error('Error fetching posts:', error);
    return [];
  }
}

export async function fetchPostBySlug(slug: string) {
  try {
    const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/posts?filters[Slug][$eq]=${slug}&populate=*`);
    if (!res.ok) {
      throw new Error(`HTTP error! status: ${res.status}`);
    }
    const data = await res.json();
    return data.data?.[0] || null;
  } catch (error) {
    console.error('Error fetching post by slug:', error);
    return null;
  }
}
